@charset "utf-8";

html { -ms-touch-action: none;}
body {font-family: "Microsoft YaHei",sans-serif;color:#333;font-size:0.6rem;}
a {text-decoration: none;-webkit-touch-callout: none;}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, span, a {margin: 0; padding: 0;}
table {border-collapse: collapse; border-spacing: 0;}
fieldset, img, iframe {border: 0;}
:focus {outline: 0;}
i, address, caption, cite, code, dfn, em, strong, th, var {font-style: normal;}
ol, ul, li {list-style: none;}
caption, th {text-align: left;}
h1, h2, h3, h4, h5, h6 {font-size: 100%; font-weight: normal;}
blockquote::before, blockquote::after, q::before, q::after {content: '';}
abbr, acronym {border: 0; font-variant: normal;}
img {vertical-align: top; border: none;}
input {font-family: "Microsoft YaHei",sans-serif; -webkit-user-select: text; -ms-user-select: text; user-select: text;}
textarea {-webkit-user-select: text; -ms-user-select: text; user-select: text; -webkit-appearance: none;}
input:focus, select:focus, textarea:focus, button:focus {outline: none;}
input[type="text"], input[type="password"], input[type="search"], input[type="tel"], input[type="number"], input[type="email"], input[type="url"], textarea {-webkit-appearance: none; border: 1px solid; border-radius: 0;}
input[type="number"]::-webkit-inner-spin-button {-webkit-appearance:none;}
em, i {font-style: normal;}
input[type="checkbox"] {position: relative; width: 0.75rem; height: 0.75rem; background-color: #F5F5F5; border: 0.05rem solid #CCC; -webkit-border-radius: 50%; border-radius: 50%; vertical-align: middle;}
input[type="checkbox"]:checked {border-color: #ED5564; background-color: #ED5564;}
input[type="checkbox"]:checked::after {content: ''; position: absolute; left: 0.1rem; top: 0.14rem; width: 0.4rem; height: 0.2rem; border-left: 0.065rem solid #fff; border-bottom: 0.065rem solid #fff; -webkit-transform: rotate(-45deg); -ms-transform: rotate(-45deg); transform: rotate(-45deg);}
sup {vertical-align: text-top;}
sub {vertical-align: text-bottom;}
/*取消ios点击事件半透明背景*/
*{-webkit-tap-highlight-color:transparent;box-sizing: border-box;}
/*公共样式*/
.align-center{display: flex;  justify-content: center;  align-items: center;}
.no-scroll{width:100%;height:100vh;overflow: hidden}
/*文章详情页样式*/
.article-content .essay img{
    width:100%;
}
.article-content .essay h1{
    padding:0.5rem 0;
    font-size:0.9rem;
    font-weight: bold;
    line-height:1.2rem;
}
.article-content .essay h2{
    padding:0.3rem 0;
    font-size:0.8rem;
    font-weight: bold;
    line-height:1rem;
}
.article-content .essay h3{
    padding:0.2rem 0;
    font-size:0.7rem;
    font-weight: bold;
    line-height:1rem;
}
.article-content .essay h4{
    font-size:0.6rem;
    font-weight: bold;
    line-height:0.8rem;
}
.article-content .essay pre{
    width:100%;
    padding:0.5rem;
    overflow-x:scroll;
    background-color:#F5F5F5;
}
.article-content .essay pre code{
    font-size:0.5rem;
}
.article-content .essay ul{
    width:100%;
}
/*评论*/
.reply-content img{
    width:100%;
}
.reply-content p{
    width:100%;
    white-space: pre-wrap;
    word-break: break-all;
}



